@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');

*{
    margin: 0;
    padding:0;
    box-sizing:border-box;
}

/* font-family: 'Playfair Display', serif; */
/* font-family: 'Poppins', sans-serif; */
/* navbar css */
.navbar{
    font-family: 'Poppins', sans-serif;
    font-size: 17px;
    line-height: 26px;
    text-transform: uppercase;
    box-shadow: 0 8px 6px -6px gray;
    --webkit-box-shadow : 0 8px 6px -6px gray;
    border-bottom:0.0px solid gray !important ;
}

.nav-link{
    font-weight: 300 !important;
}

.active{
    border-bottom: 2px solid black
}

.navbar-brand{
    font-weight: 700;
    font-family:'roboto' , sans-serif;
    letter-spacing: 0.8px;
}

.footer {
    color: white;
    padding: 25px;
    background: #000000; /* fallback for old browsers */
    background: -webkit-linear-gradient(
        to right,
        #434343,
        #000000
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #434343, #000000);
}

.footer a {
    text-decoration: none;
    color: white;
    padding: 10px;
}

.footer a:hover {
    color: #ffefba;
    border-bottom: 1px solid #ffefba;
}


.pnf {
    display: flex;
    min-height: 65vh;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.pnf-title {
    font-size: 100px;
    font-weight: 700;
}

.pnf-heading {
    font-weight: normal;
}

.pnf-btn {
    color: black;
    border: 1px solid black;
    text-decoration: none;
    padding: 10px;
    margin-top: 10px;
}
.pnf-btn:hover {
    background-color: black;
    color: white;
}




.contactus {
    margin: 0;
    padding: 0;
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* register page */
.register{
    display: flex;
    align-items: center;
    justify-content: center;
    height:70vh;
    flex-direction: column;
}
.grid-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr); /* Three columns with equal width */
  grid-gap: 10px; /* Gap between grid items */
}

.grid-item {
  background-color: #f0f0f0;
  padding: 20px;
  text-align: center;
}

.button-overlay {
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button-overlay:hover {
    background-color: #45a049;
  }
  .card1 {
    padding-bottom: 20px; /* Adds padding at the bottom of the first div */
  }
  
  .card2 {
    padding-top: 20px; /* Adds padding at the top of the second div */
  }
  .container {
    display: flex; /* Use Flexbox */
    justify-content: space-between; /* Distribute items evenly with space between */
  }
  
  .card {
    border: 2px solid #ccc;
    border-radius: 8px;
    padding: 10px;
    width: 50%; /* Adjust card width as needed */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }